.trending {
  
    display: flex;
    flex-direction: column;
    gap: 2rem;
    .title-container {
      padding-top: 2rem;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      .title {
          text-align: center;
        color: var(--accent-color1);
        font-size: 2.4rem;
      }
      .description {
        color: var(--accent-color3);
        a {
          color: var(--accent-color3);
          font-weight: bold;
        }
      }
    }
    .cards {
      display: flex;
      justify-content: space-evenly;
      gap: 5rem;
    }
  }
  
  @media screen and (min-width: 280px) and (max-width: 1080px) {
    .super-rare {
      padding: 1rem 2rem;
      width: 100vw;
      .cards {
        overflow: auto;
        justify-content: flex-start;
        gap: 2rem;
        padding-left: 1.5rem;
        &::-webkit-scrollbar {
          display: none;
        }
        .card {
          zoom: 0.7;
        }
      }
    }
  }
  